.landing {
  background-image: -webkit-linear-gradient(#e1eaef, #e4f3ec);
  background-image: linear-gradient(#e1eaef, #e4f3ec);
  height: 100vh; }

.landing-content {
  height: 70%;
  margin-top: 15%; }
  .landing-content .author {
    font-size: calc(13px + (26 - 13) * ((100vw - 300px) / (1600 - 300)));
    font-family: 'Raleway';
    color: white; }
  .landing-content .title {
    font-size: calc(36px + (72 - 36) * ((100vw - 300px) / (1600 - 300)));
    color: white; }
  .landing-content .subtitle {
    font-size: calc(16px + (32 - 16) * ((100vw - 300px) / (1600 - 300)));
    color: white; }
  .landing-content .call-to-action {
    font-family: 'Raleway';
    color: white;
    font-size: calc(10px + (20 - 10) * ((100vw - 300px) / (1600 - 300))); }
  .landing-content .lighter-weight {
    font-weight: lighter;
    margin: 0 5px;
    color: white; }

@media only screen and (min-width: 768px) {
  .landing-content {
    height: 60%;
    max-width: 75%;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto; } }

.landing-icon-container {
  width: 120px;
  height: 120px;
  border: 2px solid white;
  border-radius: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  -webkit-animation: pulse-border 2s infinite;
          animation: pulse-border 2s infinite;
  cursor: pointer; }

@media only screen and (min-width: 768px) {
  width: 160px;
  height: 160px; }

@-webkit-keyframes pulse-border {
  0% {
    border-color: white; }
  50% {
    border-color: rgba(255, 255, 255, 0); }
  100% {
    border-color: white; } }

@keyframes pulse-border {
  0% {
    border-color: white; }
  50% {
    border-color: rgba(255, 255, 255, 0); }
  100% {
    border-color: white; } }

.landing-icon {
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
  -webkit-animation: rotation 240s infinite;
          animation: rotation 240s infinite; }

@-webkit-keyframes rotation {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes rotation {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.not-scrolling {
  background-position: 0 0%;
